@import url(https://fonts.googleapis.com/css2?family=Oxygen:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar ul {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  list-style: none;
}

.cart {
  position: relative;
}

.cart img {
  width: 50px;
  height: 50px;
}

.item-quantity {
  position: absolute;
  top: -20px;
  right: -10px;
  color: red;
}

.cart-container {
  margin: 30px 0;
  padding: 5px;
}

table {
  margin-bottom: 27.5px;
  width: 100%;
  border-collapse: collapse;
  border-spacing: 0;
}

th {
  font-weight: 400;
  padding: 27.5px 0;
}

th {
  text-align: left;
}

tbody {
  width: 100%;
  padding: 0;
}

tbody tr {
  border-top: 1px solid rgb(142, 142, 142);
  margin: 20px 0;
}

.cart-img-wrapper {
  max-width: 50px;
  margin: 0;
  padding: 0;
}

.cart-img-wrapper img {
  width: 100%;
  height: auto;
  display: block;
}

.cart-item-details {
  text-align: left;
}

.cart-items p {
  display: inline;
  margin: 0;
}

.cart-item {
  list-style: none;
  font-weight: 700;
  color: var(--black);
  display: -webkit-flex;
  display: flex;
  margin-bottom: 8px;
}

.cart-item a > img {
  border: 1px solid var(--black);
  padding: 24px;
  border-radius: var(--border-radius);
  width: 100%;
  height: auto;
  box-sizing: border-box;
}

.total-container {
  border-top: 1px solid #333;
  border-bottom: 1px solid #333;
  margin: 30px 0;
}

.total-price {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.catalog {
}

.collection-header {
  margin-bottom: 40px;
  padding-top: 40px;
  text-align: center;
}
.catalog-container {
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(225px, 1fr));
  grid-gap: 12px 12px;
  gap: 12px 12px;
  padding: 0;
}

.catalog-container img {
  display: block;
  margin: 10px 0px;
  width: 100%;
}

.catalog-container li {
  -webkit-flex: 1 1;
          flex: 1 1;
  list-style: none;
}

.item {
  padding: 0;
}

.item li {
  list-style: none;
  font-weight: 700;
  color: var(--black);
  display: -webkit-flex;
  display: flex;
  margin-bottom: 8px;
}

.item img {
  border: 1px solid #333;
  border-radius: 5px;
  width: 100%;
  height: auto;
  display: block;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body,
#root {
  height: 100%;
  font-family: "Oxygen", sans-serif;
}

.content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: #141c3a;
  max-width: 1600px;
  width: 90%;
  margin: auto;
  margin-bottom: 40px;
}

h1 {
  text-align: center;
  margin: 30px 0;
}

#search-container {
  width: 60%;
  margin: 30px auto;
}

#search-container input {
  width: 100%;
  padding: 9px 25px 9px 40px;
  border-radius: 4px;
  border-radius: var(--border-radius);
  border: solid 1px #141c3a;
  background-color: #ffffff;
  outline: none;
  box-sizing: border-box;
}

.currency-filters-container {
  margin-bottom: 24px;
  text-align: center;
}

.currency-filters-container h3 {
  margin-bottom: 4px;
}

/* Mochita Store Styles */
:root {
  --black: #131b3c;
  --purple: #7000ec;
  --border-radius: 4px;
}

.currency-button {
  background: white;
  border: 1px solid #131b3c;
  border: 1px solid var(--black);
  padding: 8px;
  font-weight: 600;
  cursor: pointer;
}

.currency-button:first-of-type {
  border-top-left-radius: 4px;
  border-top-left-radius: var(--border-radius);
  border-bottom-left-radius: 4px;
  border-bottom-left-radius: var(--border-radius);
}

.currency-button:last-of-type {
  border-top-right-radius: 4px;
  border-top-right-radius: var(--border-radius);
  border-bottom-right-radius: 4px;
  border-bottom-right-radius: var(--border-radius);
}

.currency-button.selected {
  background: #131b3c;
  background: var(--black);
  color: white;
}

.price {
  color: #7000ec;
  color: var(--purple);
}

.add-to-cart-button,
button {
  background: none;
  color: #131b3c;
  color: var(--black);
  border: 1px solid #131b3c;
  border: 1px solid var(--black);
  border-radius: 4px;
  border-radius: var(--border-radius);
  padding: 4px 8px;
  font-weight: 600;
  margin-top: 12px;
  cursor: pointer;
}

.add-to-cart-button:hover,
button:hover {
  color: white;
  background: #131b3c;
  background: var(--black);
}

.total {
  display: -webkit-flex;
  display: flex;
  margin: 0;
  font-size: 1.8rem;
}

.total-value {
  color: #7000ec;
  color: var(--purple);
  margin-left: auto;
}

/* Item details */
.item-details-container {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  margin: 60px 0;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.img-container {
  -webkit-flex-basis: 40%;
          flex-basis: 40%;
}

.img-container img {
  width: 100%;
  height: auto;
}

.item-detail {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-basis: 60%;
          flex-basis: 60%;
  justify-self: center;
  text-align: center;
}

